<style>
.breadcrumb {
  margin-bottom: 20px;
}

.logo-father {
  overflow: auto;
  height: 100%;
  padding: 15px;
}

.banner-wrap {
  width: 500px;
  margin: 60px auto 0;
}

.banner-uploader {
  text-align: center;
}

.banner-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}

.banner-uploader .el-upload:hover {
  border-color: #20a0ff;
}

.banner-uploader .banner-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.banner {
  width: 100%;
  display: block;
}
</style>

<template>
  <section class="logo-father">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{'path': '/bannerlist'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="banner-wrap">
      <el-upload class="banner-uploader" name="image" :action="host" :multiple="false" accept="image/jpg,image/png,image/jpeg" :show-file-list="false" :on-success="handleSuccess" :before-upload="beforeUpload">
        <img v-if="bannerForm.url" :src="bannerForm.url" class="banner">
        <i v-else class="el-icon-plus banner-uploader-icon"></i>
      </el-upload>
      <el-form label-position="top" label-width="80px" :rules="rules" :model="bannerForm">
        <el-form-item label="" prop="title">
          <el-input v-model="bannerForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="bannerForm.type" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="bannerForm.sheng" placeholder="" @change="selectSheng">
            <el-option v-for="(item,index) in shengs" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="bannerForm.shi" placeholder="" @change="selectShi">
            <el-option v-for="(item,index) in shis" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="bannerForm.xian" placeholder=""  @change="selectXian">
            <el-option v-for="(item,index) in xians" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="link_url">
          <el-input v-model="bannerForm.link_url"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit()" style="width: 100%;margin-top: 20px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      bannerForm: {
        title: "",
        url: "",
        link_url: "",
        type: "1",
        sheng: "",
        shi: "",
        xian: "",
        id: "",
        state: 0
      },

      host: this.$common.host + "upload",

      shengs: [],
      shis: [],
      xians: [],
      noxian: false,

      //
      currentArea: null,

      rules: {
        name: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },

  created() {
    const img = this.$route.params.img;
    if (img) {
      const cityArr = img.parents.split(",");
      this.bannerForm.sheng = Number(cityArr[0]);
      this.bannerForm.shi = Number(cityArr[1]);
      if (img.city_id == cityArr[1]) {
        this.bannerForm.xian = "";
        this.noxian = true;
      } else {
        this.bannerForm.xian = img.city_id;
      }
      this.$api.getUsDistrict({ pid: cityArr[0] }, res => {
        this.shis = res.data.data;
      });
      this.$api.getUsDistrict({ pid: cityArr[1] }, res => {
        this.xians = res.data.data;
      });
      this.bannerForm.title = img.title;
      this.bannerForm.url = img.url;
      this.bannerForm.link_url = img.link_url;
      this.bannerForm.id = img.id;
      this.bannerForm.state = img.state;
    }
    this.$api.getUsDistrict("", res => {
      this.shengs = res.data.data;
    });
  },

  methods: {
    /*
      
    */
    beforeUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },

    /*
      
    */
    handleSuccess(res, file) {
      this.bannerForm.url = res.data.base_url;
    },

    /*
    * 
    */

    selectSheng(id) {
      this.bannerForm.shi = "";
      this.bannerForm.xian = "";
      this.xians = [];
      this.currentArea = this.shengs[this.bannerForm.sheng];
      this.$api.getUsDistrict({ pid: id }, res => {
        this.shis = res.data.data;
      });
    },

    /*
    * 
    */

    selectShi(id) {
      const shi = this.bannerForm.shi;
      if (!shi) {
        return false;
      }
      this.currentArea = this.shis[shi];
      this.$api.getUsDistrict({ pid: id }, res => {
        this.xians = res.data.data;
        if (!this.xians.length) {
          this.noxian = true;
        }
      });
    },

    /*
    * 
    */

    selectXian() {
      const xian = this.bannerForm.xian;
      if (!xian) {
        return false;
      }
      this.currentArea = this.xians[xian];
    },

    /*
      
    */
    onSubmit() {
      if (!this.bannerForm.title || !this.bannerForm.url) {
        this.$message({
          message: "",
          showClose: true,
          type: "warning"
        });
      } else if (!this.bannerForm.xian && !this.noxian) {
        this.$message({
          message: "",
          showClose: true,
          type: "warning"
        });
      } else {
        let postData = {
          title: this.bannerForm.title,
          city_id: this.noxian ? this.bannerForm.shi : this.bannerForm.xian,
          type: this.bannerForm.type,
          url: this.bannerForm.url,
          link_url: this.bannerForm.link_url,
          id: this.bannerForm.id,
          state: this.bannerForm.state,
          parents: this.bannerForm.sheng + "," + this.bannerForm.shi
        };

        this.$api.postAdvert(postData, res => {
          this.$message({
            message: "",
            showClose: true,
            type: "success"
          });
          this.$router.push("/bannerlist");
        });
      }
    }
  }
};
</script>



