<style scoped>
.check-in-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}

.ql-container .ql-editor {
  min-height: 20em;
  padding-bottom: 1em;
  max-height: 25em;
}

.html {
  height: 9em;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-top: none;
  resize: vertical;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="check-in-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="check-in-form">
      <el-form label-position="top" label-width="80px" :model="shareForm" :rules="rules" ref="shareForm">
        <el-form-item label="" prop="status">
          <el-tooltip :content="'' + checkSwitchText[shareForm.status]" placement="top">
            <el-switch v-model="shareForm.status" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0" on-text="" off-text="" @change="sacnSwitchFnc">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-date-picker v-model.number="shareForm.region" type="datetimerange" :picker-options="dateRangeOption" placeholder="" align="left" :disabled="shareForm.status == 0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="point">
          <el-input v-model.number="shareForm.point" placeholder="" :disabled="shareForm.status == 0">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <div style="font-size: 14px;padding-bottom: 10px;color:#48576a;"></div>
        <div class="quill-editor-example">
          <!-- quill-editor -->
          <quill-editor ref="myTextEditor" v-model="shareForm.content" :options="editorOption">
          </quill-editor>
          <div class="html ql-editor" v-html="shareForm.content"></div>
        </div>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('shareForm')" style="width: 100%;margin-top: 20px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      checkSwitchText: ['', ''],
      shareForm: {
        status: '1',
        region: '',
        point: null,
        content: ''
      },
      editorOption: {
        placeholder: '...'
      },

      rules: {
        point: [
          { required: true, message: '' },
          { type: 'number', message: '' }
        ]
      },

      dateRangeOption: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
    }
  },

  created() {
    setTimeout(() => {
      this.loading = false
    }, 200)
  },

  methods: {

    /*
    * 
    */
    sacnSwitchFnc(value) {

    },

    /*
    * 
    */
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            type: 'success',
            message: '',
            showClose: true
          })
        } else {
          this.$message({
            type: 'warning',
            message: '',
            showClose: true
          })
          return false
        }
      })
    },

  },
}
</script>



